.hero-banner {
    @include flexBox(row,space-between,center);
    max-width: 1920px;
    margin: auto;
    background-color: $gray-39373729;
    height: 650px;
    .hero-banner-info {
        @include flexBox(column, flex-start, flex-start);
        margin-left: 20px;
        width: 552px;
        margin: auto;
        font: {
            style: $unnamed-font-style-normal; 
            variant: normal; 
            weight: $unnamed-font-weight-600; 
            size: $unnamed-font-size-48;
            family: $unnamed-font-family-source-serif-pro;
        }
        line-height: $unnamed-line-spacing-60;
        letter-spacing: $unnamed-character-spacing-0;
        color: $unnamed-color-172026;
        h1 {
            width: 442px;
            margin-bottom: 8px;
        }
        p {
            font: {
                weight: $unnamed-font-weight-normal; 
                size: $unnamed-font-size-16;
                family: $unnamed-font-family-muli;
            }
            line-height: $unnamed-line-spacing-24;
            margin-bottom: 24px;
        }
        button {
            width: 220px;
            height: 40px;
            color: $white;
            background-color: $primary-brand;
            border: none;
            box-shadow: 0px 3px 6px #39373729;
            cursor: pointer;

            &:hover {
                transition: all 0.4s ease-in-out;
                background-color: $primary-d1;
            }
        }
    }
    .hero-banner-image{
        width: 51%;
        height: 650px;
        background: transparent url('../../assets/images/three-men-clothing.png') 10% 20% no-repeat padding-box;
    }
}

.category-display-wrapper {
    @include flexBox(row, space-between, center);
    width: auto;
    height: 320px;
    margin-top: -105px;
    background-color: $white;

    .category-display-box {
        max-width: 349px;
        width: calc(100% - 5px);
        height: 320px;
        padding: 222px 13px 0 5px;

        &.box-1 {
            background: transparent url('../../assets/images/women-cloth.PNG') 0% 0% no-repeat padding-box ;
        }
        &.box-2 {
            background: transparent url('../../assets/images/men-cloth.PNG') 0% 0% no-repeat padding-box ;
        }
        &.box-3 {
            background: transparent url('../../assets/images/jwellery.PNG') 0% 0% no-repeat padding-box;
        }
        &.box-4 {
            background: transparent url('../../assets/images/electronics.PNG') 0% 0% no-repeat padding-box;
        }

        &.box-1, &.box-2, &.box-3, &.box-4 {
            background-size: contain;
        }

        .category-display-info {
            max-width: 331px;
            width: calc(100% + 5px);
            height: 98px;
            background: $grey-1B252CD9 0% 0% no-repeat padding-box;
            border-bottom: 8px solid $primary-brand;
            padding: 17px 0 0 38px;

            font: {
                style: $unnamed-font-style-normal; 
                variant: normal; 
                weight: $unnamed-font-weight-600; 
                family: $unnamed-font-family-source-serif-pro;
            }
            line-height: $unnamed-line-spacing-30;
            letter-spacing: $unnamed-character-spacing-0;
            color: $white;

            h2 {
                font-size: $unnamed-font-size-24;;
            }
            p {
                font: {
                    weight: $unnamed-font-weight-normal; 
                    size: $unnamed-font-size-16;
                    family: $unnamed-font-family-muli;
                }
                line-height: $unnamed-line-spacing-24;
                letter-spacing: 0.32px;
            }
        }
    }
}